import React, { useState } from 'react';
import { useFormik } from 'formik';
import styles from './Register.module.scss';
import * as Yup from 'yup';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';


export default function Register() {

  let navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(false);
  const [errMessage, setErrMessage] = useState('');

  async function handleRegister(values) {
    setIsLoading(true)

    let { data } = await axios.post('https://ecommerce.routemisr.com/api/v1/auth/signup', values).catch((err) => {
      setIsLoading(false);
      setErrMessage(err.response?.data?.message)
      console.log(data)

    })


    if (data.message === 'success') {
      navigate('/login')
    }

  }

  let validation = Yup.object({
    name: Yup.string().required('Name is required').min(3, 'Name must be more than 2 characters').max(21, "Name can't be more than 20 characers"),
    email: Yup.string().required('E-mail is required').matches(/^[a-zA-Z0-9._%+-]+@[a-zA-Z0-9.-]+\.[A-Za-z]{2,4}$/i, 'Invalid e-mail address'),
    // email('Invalid e-mail address'),
    password: Yup.string().required('Password is required').matches(/^[A-Z][a-z0-9]{7,}$/, 'Password must start with a Capital letter and at least 8 character'),
    rePassword: Yup.string().required('rePassword is required').oneOf([Yup.ref('password')], 'rePassword does not match'),
    phone: Yup.string().required('Phone is required').matches(/^01[0125][0-9]{8}$/, 'Invalid Phone number'),
  })

  let formik = useFormik({
    initialValues: {
      name: '',
      phone: '',
      email: '',
      password: '',
      rePassword: ''
    },
    validationSchema: validation,
    onSubmit: handleRegister,

  });
  return (
    <div className='w-75 mx-auto py-5'>
      <h3 className='text-center mb-3'>Registration Form : </h3>

      <div>
        {errMessage.length > 0 ? <div className='alert alert-danger'>{errMessage}</div> : null}
      </div>

      <form onSubmit={formik.handleSubmit}>
        <label htmlFor="name">Name: </label>
        <input onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.name} type="text" name='name' id='name' className='form-control my-2' />
        {formik.errors.name && formik.touched.name ? <div className="alert alert-danger">{formik.errors.name}</div> : ""}

        <label htmlFor="email">Email: </label>
        <input onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.email} type="email" name='email' id='email' className='form-control my-2' />
        {formik.errors.email && formik.touched.email ? <div className="alert alert-danger">{formik.errors.email}</div> : null}


        <label htmlFor="password">Password: </label>
        <input onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.password} type="password" name='password' id='password' className='form-control my-2' />
        {formik.errors.password && formik.touched.password ? <div className="alert alert-danger">{formik.errors.password}</div> : null}

        <label htmlFor="rePassword">rePassword: </label>
        <input onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.rePassword} type="password" name='rePassword' id='rePassword' className='form-control my-2' />
        {formik.errors.rePassword && formik.touched.rePassword ? <div className="alert alert-danger">{formik.errors.rePassword}</div> : null}

        <label htmlFor="phone">Mobile: </label>
        <input onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.phone} type="tel" name='phone' id='phone' className='form-control my-2' />
        {formik.errors.phone && formik.touched.phone ? <div className="alert alert-danger">{formik.errors.phone}</div> : null}

        {isLoading ? <button type='button' className='text-white btn bg-main my-2'><i className='fas fa-spinner fa-spin'></i></button> :
          <button disabled={!(formik.isValid && formik.dirty)} type='submit' className='text-white btn bg-main my-2'>Submit</button>}

      </form>
    </div>
  )
}
